<template>
  <div id="transkribusEditor">
    <Editor v-if="xmlJsonString" :iiifUrl="iiifUrl" :xmlJson="toJsonObject" />
    <Editor v-else :iiifUrl="iiifUrl" :xmlJson="xmlJson" />
  </div>
</template>

<script>
// Test page : http://localhost:5000/collection/76950/doc/497355/detail/1

import Editor from "./views/Editor.vue";

const json = require("./assets/page1.json");

export default {
  name: "transkribusEditor",
  props: {
    iiifUrl: {
      type: String,
      default: () =>
        "https://files.transkribus.eu/iiif/2/XITIHIQXRRBAHPZDHDPHWHMP/info.json",
    },
    xmlJson: {
      type: Object,
      default: () => json,
    },
    xmlJsonString: {
      type: String,
    },
    xml: {
      type: String,
    },
    tags: {
      type: String,
    },
  },
  methods: {
    escapeSpecialChars(string) {
      return string
        .replace(/\\n/g, "\\n")
        .replace(/\\'/g, "\\'")
        .replace(/\\"/g, '\\"')
        .replace(/\\&/g, "\\&")
        .replace(/\\r/g, "\\r")
        .replace(/\\t/g, "\\t")
        .replace(/\\b/g, "\\b")
        .replace(/\\f/g, "\\f");
    },
  },
  components: {
    Editor,
  },
  computed: {
    toJsonObject() {
      var cleanedString = this.escapeSpecialChars(this.xmlJsonString);
      const obj = JSON.parse(cleanedString);
      return obj;
    },
  },
  mounted() {
    if (this.iiifUrl) {
      this.$store.dispatch("content/setIIIF", {
        iiifUrl: this.iiifUrl,
      });
    }
    if (this.xml) {
      this.$store.dispatch("content/setXml", {
        xml: this.xml,
      });
    }
    if (this.xmlJsonString) {
      this.$store.dispatch("content/setXmlJson", {
        xmlJson: this.toJsonObject,
      });
    }
    if (this.tags) {
      var cleanedString = this.escapeSpecialChars(this.tags);
      const obj = JSON.parse(cleanedString);
      this.$store.dispatch("browse/setTagDefs", { tagDefs: obj });
    }
  },
};
</script>
