export async function fetchSearchResults(
  { term, documents, titles, page, fuzziness },
  { offset = 0, limit = 10 }
) {
  const res = await window.$nuxt.$axios(
    "https://transkribus.eu/r/search/search",
    {
      method: "POST",
      body: JSON.stringify({
        term,
        collections: [collection],
        documents,
        titles,
        page,
        fuzziness,
        offset,
        limit,
      }),
      headers: {
        "Content-Type": CONTENT_TYPE,
        Accept: CONTENT_TYPE,
      },
    }
  );
  if (res.status === 200) return res.data;
  if (res.status === 401) {
    logOut();
  } else throw new Error(res);
}

export async function fetchSearchFacets({
  term,
  documents,
  titles,
  fuzziness,
}) {
  const res = await window.$nuxt.$axios(
    "https://transkribus.eu/r/search/facets",
    {
      method: "POST",
      body: JSON.stringify({
        term,
        collections: [collection],
        documents,
        titles,
        fuzziness,
      }),
      headers: {
        "Content-Type": CONTENT_TYPE,
        Accept: CONTENT_TYPE,
      },
    }
  );
  const data = await res.json();
  if (res.status === 401) {
    logOut();
  }
  const { total, facets, params } = data;
  return {
    total,
    facets,
    params,
  };
}

